@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: "Roboto", sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: white solid 60px;
}

.App:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/static/media/Background.56b9c1b9.jpg) no-repeat center center;
  background-size: cover;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  width: 100%;
}

.Buttonstyle {
  margin: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  border: none;
  padding: 10px 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 2%;
  background: black;
  color: white;
  border: white solid 2px;
}

.Buttonstyle:hover {
  background: white;
  color: black;
  border: black solid 2px;
}

.Button {
  position: relative;
  bottom: 20px;
  padding-top: 20px;
  margin-top: 60px;
}

.Text {
  background: white;
  width: 600px;
  padding: 10px;
  margin-bottom: 30px;
  font-family: "Roboto", sans-serif;
  text-align: center;
}

.Socials-icons {
  width: 80px;
}

.Homepage {
  background: white;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  margin-bottom: 30px;
}

@media screen and (max-width: 968px) {
  .Homepage {
    width: 60%;
    height: auto !important;
  }
}

@media screen and (max-width: 768px) {
  .Homepage {
    width: 75%;
    height: auto !important;
  }
  .App {
    border: white solid 0px;
  }

  .Buttonstyle {
    margin: 10px;
    margin-top: 30px;
    padding: 5px 20px;
  }
}

.nav {
  position: fixed;
  background-color: white;
  height: 60px;
  width: 100%;
  z-index: 1000;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Logoleft {
  font-family: "Roboto", sans-serif;
  padding-top: 5px;
  font-weight: bold;
  color: black;
  margin-left: 60px;
}

.link {
  text-decoration: none;
  color: black;
}

@media screen and (max-width: 768px) {
  .Logoleft {
    margin-left: 10px;
  }
}

.Logoright {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  padding-top: 5px;
  color: black;
  margin-right: 60px;
}

@media screen and (max-width: 768px) {
  .Logoright {
    margin-right: 10px;
  }
}

.App-picture {
  width: 75%;
  object-fit: contain !important;
}

.Title {
  color: black;
  text-align: center;
  font-weight: bold;
}

@media screen and (max-width: 968px) {
  .App-picture {
    width: 75%;
    height: auto !important;
    object-fit: contain !important;
  }
}

@media screen and (max-width: 768px) {
  .App-picture {
    width: 80%;
    height: auto !important;
    object-fit: contain !important;
  }
}

hr {
  width: 200px;
  margin-top: -2px;
}

